import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { Button, Col, Container, Row, ProgressBar } from 'react-bootstrap';
import RecommendService from '../../../services/recommend.service';
import { useNavigate } from 'react-router-dom';
import Error from './Error';
import ReactTooltip from 'react-tooltip';

export default function Result() {
    // 
    const [thisResult, setThisResult] = useState(false); //  
    const [isError, setIsError] = useState(false); //    
    const [answer, setAnswer] = useState(false); //   

    //
    let testData = {
        "list":[
            {
                "academicName":"",
                "departmentList":"AI,,,,"
            },
            {
                "academicName":"",
                "departmentList": ", , , , , , pr, , GBT"
            }
        ]
    }

    //   callback 
    let navigate = useNavigate();


    useEffect(() => {
        //     
        // RecommendService.getFirstSectionResult(sessionStorage.getItem('result1Type')).then(
        //     (response) => {
        //         //   
        //         setThisResult(response.data);
        //         //
        //         ShowResult();
        //     }
        // ).catch(
        //     (Error) => {
        //         //  
        //         setIsError(true);
        //     }
        // )

        //()
        setThisResult(testData.list);

        ShowResult();
        //()
    
    },[])



    const ShowResult = () => {
        console.log('thisResult:',thisResult);
        console.log('testData.list.academicName:',testData.list[0]);

        if(!thisResult){
            return;
        }

        return(
            <>
                {
                    testData.list.map(thisData => (
                        <>
                            <div className='eachResult' key={thisData.academicName} data-tip data-for={`tooltip${thisData.academicName}`} onClick={()=>{selectAcademicName(thisData.academicName)}}>
                                
                                    {thisData.academicName}
                                
                                    <ReactTooltip
                                        id={`tooltip${thisData.academicName}`}
                                        effect="solid"
                                        place="bottom"
                                        type="dark"
                                        key={thisData.departmentList}
                                        >
                                        {thisData.departmentList}
                                    </ReactTooltip>
                            </div>
                        </>
                    ))
                }
            </>
        )
    }

    const selectAcademicName =(thisAcademicName) =>{
        console.log("thisAcademicName:",thisAcademicName);
        setAnswer(thisAcademicName);

        console.log("setAnswer:",answer);
    }
 
    
    const goToNext = () => {
        //      
        if(answer !== false){
            //API
            RecommendService.submitFirstSectionAnswer(answer);
            console.log("answer:",answer);

            sessionStorage.setItem('recommendFirstResult', 'true');
            sessionStorage.setItem('questionNum', '1');

            //2  page 
            navigate("/question2");
            window.location.reload();
        }
    }



  return (
    <BodyBlock>
        <div className="container">
            <div className='notice'>
                <span> 1 !</span><br/>
                <span>    .</span>
            </div>
            <div className='resultFrame'>
                {
                    !isError?
                    <>
                        {
                            !thisResult?
                            <></>:
                            <ShowResult/>  
                        }
                    </>:
                        <Error/>
                }
            </div>
            <div className='nextButtonFrame'>
            <Button className='nextButton' onClick={() => goToNext()}></Button>
            </div>
        </div>
    </BodyBlock>
  )
}


//CSS
const BodyBlock = styled.div`
    .container{
        display: grid;
        grid-template-rows: 1fr 5fr 1fr;
        background-color: white;
        text-align: center;
        /*justify-content: center;*/
        
        
        /*vertical-align: middle;*/
        row-gap: 10px;

        height: 70vh;
        width: 45vh;
    }
    

    /**/
    .notice{
        grid-row-start: 1;
        grid-row-end: 2;

        /*grid-template-rows: repeat(auto-fit, minmax(300px, auto));*/

        align-items: center;
        
        /**/
        padding-top: 3%;
        font-size: 15px;
        color: #5a5a5a;

    }

    /**/
    .resultFrame{
        grid-row-start: 2;
        grid-row-end: 3;
        /*grid-template-rows: repeat(auto-fit, minmax(300px, auto));*/

        //  
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;

        .eachResult:nth-child(odd){

            background-color: #002F5A;
            opacity: 0.8;
        
            /**/
            border-radius: 5px;
            height: 3em;
            width: 50%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
            padding-top: 10px;
    
            /**/
            &:hover {
                background-color: #002F5A;
                opacity: 1;
            }
        }

        .eachResult:nth-child(even){

            background-color: #028799;
            opacity: 0.8;
        
            /**/
            border-radius: 5px;
            height: 3em;
            width: 50%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
            padding-top: 10px;
    
            /**/
            &:hover {
                background-color: #028799;
                opacity: 1;
            }
        }
    }

   
    /* */
    .nextButtonFrame{
        grid-row-start: 3;
        grid-row-end: 4;
        /*grid-template-rows: repeat(auto-fit, minmax(300px, auto));*/

        //  
        display: flex;
        justify-content: center;
        align-items: center;

        .nextButton{

            background-color: #002F5A;
            opacity: 0.8;
        
            /**/
            border-radius: 5px;
            width: 40%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
    
            /**/
            &:hover {
                background-color: #002F5A;
                opacity: 0.9;
            }
        }
    }
`